/* Core */
import { Component, ChangeDetectorRef, NgZone, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { IonicPage, NavController, NavOptions, NavParams, PopoverOptions, PopoverController } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Services */
import { ClipsService } from '../../app/services/clips.service';
import { LocalStorageService } from '../../app/services/localStorage.service';
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { AbstractAuthenticationService } from '../../app/services/abstract-auth.service';
import { ChannelsService } from '../../app/services/channels.service';
import { CurrentChannelStoreService } from '../../app/services/current-channel.store.service';

@IonicPage({
  defaultHistory: ['channels'],
  name: 'trending-channel',
  segment: 'trending/channel/:channel'
})
@Component({
  selector: 'page-trending-channel',
  templateUrl: 'trending-channel.html'
})
export class TrendingChannelPage implements OnInit {
  public isFavorite: boolean = false;
  private cursor: string;
  private ngZone: NgZone;
  private sanitizer: DomSanitizer;
  private channel: string;
  public clips = [];
  public hasClips = true;
  private id: string;
  private showMore: boolean = true;

  public period: string = "day";

  constructor(
    public navCtrl: NavController,
    private _sanitizer: DomSanitizer,
    private _ngZone: NgZone,
    private navParams: NavParams,
    private popoverCtrl: PopoverController,

    /* Services */
    public authService: AbstractAuthenticationService,
    private channelsService: ChannelsService,
    private clipsService: ClipsService,
    private localStorageService: LocalStorageService,
    private loaderService: LoaderService,
    private toastService: ToastService,
    
    /* Stores */
    private currentChannelStoreService: CurrentChannelStoreService
    ) {
    this.channel = navParams.get('channel');
    this.sanitizer = _sanitizer;
    this.ngZone = _ngZone;
  }

  ngOnInit(): void {
    (async () => {
      this.period = (await this.localStorageService.getItem("trending-channel-period")) || "day";

      await this.getTrendingByChannel(false);

      if (!this.currentChannelStoreService.getId() && this.clips.length) {
        this.currentChannelStoreService.setId(this.clips[0].broadcaster.id);
      }

      if (this.authService.loggedIn) {
        this.isFavorite = await this.channelsService.getIsFavorite(this.currentChannelStoreService.getId());
      }
    })();
  }

  public addFavorite() {
    const promise = this.channelsService.addFavorite(this.currentChannelStoreService.getId());

    promise.then(() => {
      this.isFavorite = true;
      this.toastService.show('Added channel to favorites.');
    });

    this.loaderService.show(promise);
    this.toastService.onFailure(promise);
  }

  public removeFavorite() {
    const promise = this.channelsService.removeFavorite(this.currentChannelStoreService.getId());

    promise.then(() => {
      this.isFavorite = false;
      this.toastService.show('Removed channel from favorites.');
    });

    this.loaderService.show(promise);
    this.toastService.onFailure(promise);
  }

  getTrendingByChannel(refresh: boolean): Promise<any> {
    let that = this;
    let promise;

    promise = this.clipsService.getTrendingByChannel(that.channel, that.cursor, that.period).then(function(trendingClips) {
      let clips = trendingClips.clips;

      if (!clips.length) {
          that.hasClips = false;
      }

      clips.forEach(function(clip) {
         let src = 'https://clips.twitch.tv/embed?clip=' + clip.slug + '&tt_medium=clips_api&tt_content=embed&autoplay=false';
         clip.embedHtml = that.sanitizer.bypassSecurityTrustHtml("<div style='overflow: hidden; padding-bottom: 56.25%; position: relative; height: 0;' class='video-responsive'><iframe style='left: 0; top: 0; height: 100%; width: 100%; position: absolute;' src='" + src + "' width='100%' height='100%' frameborder='0' scrolling='no' allowfullscreen='true' preload='metadata'></iframe></div>");
      });

      that.ngZone.run(() => {
        if (refresh) {
          that.clips.splice(0, that.clips.length);
        }
        clips.forEach(function(clip) {
          that.clips.push(clip);
        })

        that.cursor = trendingClips.cursor;
        that.showMore = !!that.cursor;
      });
    });

    that.loaderService.show(promise);
    that.toastService.onFailure(promise);

    return promise;
  }

  presentPopover(myEvent) {
    let that = this;
    let popoverOptions: PopoverOptions = {};
    popoverOptions.enableBackdropDismiss = true;
    let popover = this.popoverCtrl.create('filter-popover', {
      period: that.period,
      displayLanguages: false
    }, popoverOptions);
    popover.present({
      ev: myEvent
    });

    popover.onDidDismiss(this.refreshData.bind(that));
  }

  refreshData(data) {

    if (!data) return;

    let that = this;
    let oldPeriod = this.period;
    this.period = data.period;
    localStorage.setItem("trending-channel-period", data.period);

    if (oldPeriod !== data.period) {
      that.cursor = undefined;
      that.getTrendingByChannel(true);
    }
  }

  more() {
    this.getTrendingByChannel(false);
  }

  goToProfile() {
    this.navCtrl.push('profile');
  }
}



